import React, { useState } from 'react';
import { SketchField, Tools } from 'react-sketch';
import { Container, Grid, TextField, makeStyles, Paper, MenuItem } from '@material-ui/core'
import Header from "../components/Header/Header";

const useStyles = makeStyles(() => ({
    container: {
        padding: '88px 20px 20px'
    },
    paper: {
        marginTop: '20px',
        border: '2px solid black'
    }
}))

export default function SketchPage() {

    const classes = useStyles();
    const [selectedTool, setSelectedTool] = useState(Tools.Pencil);
    const [open, setOpen] = useState(false);

    const handleOpenNavBar = () => {
        setOpen(!open);
    }

    const selectTool = (event) => {
        setSelectedTool(event.target.value);
    }
    return (
        <div id="st-container" className={open?"st-container st-effect-1 st-menu-open":"wrapper dashboard-wrapper st-container"}>
            <Header
                header="Sketch Sandbox"
                openNavBar={handleOpenNavBar}
                open={open} />
            <Container maxWidth='md' className={classes.container}>
                <Grid container spacing={2} >
                    <Grid item xs={12}>
                        <Grid container spacing={2} >
                            <Grid item>
                                <TextField
                                    select={true}
                                    label="Canvas Tool"
                                    value={selectedTool}
                                    onChange={selectTool}
                                    helperText="Please select Canvas Tool">
                                    <MenuItem value={Tools.Select} key="Select">Select</MenuItem>
                                    <MenuItem value={Tools.Pencil} key="Pencil">Pencil</MenuItem>
                                    <MenuItem value={Tools.Line} key="Line">Line</MenuItem>
                                    <MenuItem value={Tools.Arrow} key="Arrow">Arrow</MenuItem>
                                    <MenuItem value={Tools.Rectangle} key="Rectangle">Rectangle</MenuItem>
                                    <MenuItem value={Tools.Circle} key="Circle">Circle</MenuItem>
                                    <MenuItem value={Tools.Pan} key="Pan">Pan</MenuItem>
                                    <MenuItem value={Tools.RectangleLabel} key="Pan">RectangleLabel</MenuItem>
                                </TextField>
                            </Grid>
                        </Grid>
                    </Grid>
                    <Grid item xs={12}>
                        <Paper className={classes.paper} >
                            <SketchField
                                tool={selectedTool}
                                lineColor='black'
                                lineWidth={1} />
                        </Paper>
                    </Grid>
                </Grid>
            </Container>
        </div>
    );
}
